function fullPageInit() {
	try {
		const headerHeight = $("#header").outerHeight();
		let arrayAnchor = [];
		if ($("#menu-parallax").length) {
			let menuParallax = $("#menu-parallax li");
			for (let index = 0; index < menuParallax.length; index++) {
				let item = $(menuParallax[index]);
				let val = item.attr("data-menuanchor");
				arrayAnchor.push(val);
			}
		} else {
			arrayAnchor = [
				"fp-block-1",
				"fp-block-2",
				"fp-block-3",
				"fp-block-4",
				"fp-block-5",
				"fp-block-6",
				"fp-block-7",
				"fp-block-8",
				"fp-block-9",
				"fp-block-10",
				"fp-block-11",
				"fp-block-12",
				"fp-block-13",
			];
		}
		var myFullpage = new fullpage("#fullpage", {
			bigSectionsDestination: null,
			menu: "#header,#menu-parallax",
			lockAnchors: false,
			fixedElements: "#header,#menu-parallax,.sidenav-group",
			anchors: arrayAnchor,
			navigationTooltips: arrayAnchor,
			normalScrollElements: ".scrollbar-wrap,.popup-modal",
			slidesNavigation: true,
			slidesNavPosition: "bottom",
			showActiveTooltip: true,
			//Scrolling
			css3: true,
			offsetSections: true,
			scrollingSpeed: 700,
			autoScrolling: true,
			fitToSection: false,
			paddingTop: 0,
			fitToSectionDelay: 600,
			scrollBar: false,
			easing: "easeInOutCubic",
			easingcss3: "ease",
			loopBottom: false,
			loopTop: false,
			loopHorizontal: true,
			continuousHorizontal: true,
			scrollHorizontally: true,
			continuousVertical: false,
			scrollOverflow: false,
			touchSensitivity: 15,
			normalScrollElementTouchThreshold: 5,
			//Accessibility
			keyboardScrolling: true,
			animateAnchor: true,
			recordHistory: true,
			//Design
			controlArrows: true,
			verticalCentered: true,
			resize: false,
			responsiveWidth: 0,
			responsiveHeight: 0,
			//Custom selectors
			sectionSelector: "section",
			dragAndMove: true,
			afterRender: function () {
				$("body").addClass("body-has-fp");
			},
			onLeave: function (origin, destination, direction) {
				$("section [data-aos]").removeClass("aos-animate");
				// Get the current section (the section that just became fully visible)
				var totalSections = document.querySelectorAll('#fullpage section').length;
				var currentSection = destination.item;

				// Check if the current section has the class "section-blue"
				if (currentSection.classList.contains("section-blue")) {
					$("body").addClass("fp-body-blue");
				} else {
					$("body").removeClass("fp-body-blue");
				}
				if (currentSection.classList.contains("nav-white")) {
					$("body").addClass("fp-nav-white");
					$("#menu-parallax").addClass("fp-nav-white");
				} else {
					$("body").removeClass("fp-nav-white");
					$("#menu-parallax").removeClass("fp-nav-white");
				}
				if (destination.index === 0) {
					$(".sidenav-group").removeClass("is-visible");
				} else {
					$(".sidenav-group").addClass("is-visible");
				}

			},

			afterLoad: function (origin, destination, direction) {
				$("section.active [data-aos]").addClass("aos-animate");
				var totalSections = document.querySelectorAll('#fullpage section').length;
				if (destination.index === 0) {
					var currentSection = destination.item;
					// Check if the current section has the class "section-blue"
					if (currentSection.classList.contains("section-blue")) {
						$("body").addClass("fp-body-blue");
					} else {
						$("body").removeClass("fp-body-blue");
					}
					if (currentSection.classList.contains("nav-white")) {
						$("body").addClass("fp-nav-white");
						$("#menu-parallax").addClass("fp-nav-white");
					} else {
						$("body").removeClass("fp-nav-white");
						$("#menu-parallax").removeClass("fp-nav-white");
					}
					$(".sidenav-group").removeClass("is-visible");

				} else {
					$(".sidenav-group").addClass("is-visible");
				}


			},
			onSlideLeave: function () {
				$("section [data-aos]").removeClass("aos-animate");
			},
			afterSlideLoad: function () {
				$("section.active [data-aos]").addClass("aos-animate");
			},
		});
	} catch (error) { }
}


function aosInit() {
	AOS.init({
		// Global settings:

		disable: window.innerWidth < 1200,
		startEvent: "load", // name of the event dispatched on the document, that AOS should initialize on
		initClassName: "aos-init", // class applied after initialization
		animatedClassName: "aos-animate", // class applied on animation
		useClassNames: false, // if true, will add content of `data-aos` as classes on scroll
		disableMutationObserver: false, // disables automatic mutations' detections (advanced)
		debounceDelay: 20, // the delay on debounce used while resizing window (advanced)
		throttleDelay: 0, // the delay on throttle used while scrolling the page (advanced)
		// Settings that can be overridden on per-element basis, by `data-aos-*` attributes:
		offset: 120, // offset (in px) from the original trigger point
		delay: 120, // values from 0 to 3000, with step 50ms
		duration: 500, // values from 0 to 3000, with step 50ms
		easing: "ease-in-out-back", // default easing for AOS animations
		once: true,
		mirror: true, // whether elements should animate out while scrolling past them
		anchorPlacement: "top-bottom", // defines which position of the element regarding to window should trigger the animation
	});
}

