export function basicTask() {
	const backTop = document.querySelector(".back-to-top");
	const counterItem = document.querySelectorAll(".counter");
	const setBackground = document.querySelectorAll("[setBackground]");
	const menuToggle = document.querySelector(".site-menu-toggle");
	const searchToggle = document.querySelector(".search-toggle");
	const bodyWrap = document.querySelector("body");
	const menuMobile = document.querySelector(".mobile-nav-wrap");
	const searchWrap = document.querySelector(".searchbox");
	const searchInput = searchWrap.querySelector("input");


	// Card Flex  Hover , click
	const cardHover = $('.card-option');
	const cardClick = $('.card-click');

	cardHover.on('mouseenter', function () {
		$('.card-option').removeClass('active');
		$(this).toggleClass('active');
	});
	cardClick.on('mouseenter', function () {
		$('.card-click').removeClass('active');
		$(this).toggleClass('active');
	});


	$(".filter-table-wrap").each(function () {
		let x = 10;
		$(this).find("tbody tr").slice(0, 5).show();
		let size = $(this).find("tbody tr").length;
		console.log(size);
		if (size < x || size == x) {
			$(this).find(".ajax-btn-wrap").addClass("disble-btn");
		}
		$(".filter-table-wrap")
			.find(".ajax-btn-wrap .btn-load-more")
			.on("click", function (e) {
				e.preventDefault();
				x = x + 5;
				$(this)
					.parents(".filter-table-wrap")
					.find("tbody tr")
					.slice(0, x)
					.slideDown();
				if (x >= size || size < x) {
					$(this).parent().addClass("disble-btn");
				}
			});
	});


	//Basic Toggle Function
	menuToggle.addEventListener("click", (e) => {
		e.preventDefault();
		menuMobile.classList.toggle("is-open");
		menuToggle.classList.toggle("is-active");
		bodyWrap.classList.toggle("overlay-bg");
	});


	searchToggle.addEventListener("click", () => {
		const isOpen = searchWrap.classList.toggle("is-open");
		bodyWrap.classList.toggle("overlay-bg", isOpen);

		if (isOpen) {
			// Wait for CSS transition (if any)
			setTimeout(() => {
				searchInput.focus();
			}, 0);
		}
	});
	// Back to top functionality
	if ($('body').hasClass('body-has-fp')) {
		backTop.addEventListener("click", (event) => {
			event.preventDefault();
			// Use FullPage.js's API to move to the first section
			if (typeof fullpage_api !== "undefined") {
				fullpage_api.moveTo(1); // Move to the first section
			}
		});
	} else {
		backTop.addEventListener("click", (event) => {
			event.preventDefault();
			// Scroll to the top normally
			$("html, body").animate({ scrollTop: 0 }, "300");
		});
	}


	$(".menu-item > a").wrap("<div class='title'></div>");
	$(".menu-item-has-children >.title").append("<div class='icon'></div>");
	//Tab function

	$(".mobile-menu-nav .menu-item-has-children .title .icon").on("click", function () {
		$(this).parent().next().slideToggle();
		$(this).parent().toggleClass('is-active');
	});
	//Tab function
	$(".rolling-text").each(function () {
		const $element = $(this);
		const innerText = $element.text().trim();
		$element.empty();

		const createTextContainer = () =>
			$("<div>")
				.addClass("block")
				.append(
					[...innerText].map((letter) =>
						$("<span>")
							.addClass("letter")
							.text(letter.trim() === "" ? "\xa0" : letter)
					)
				);

		$element.append(createTextContainer()).append(createTextContainer());
	});



	$(".toggle-wrap").each(function (index, element) {
		let $this = $(this);
		$this.addClass("toggle-instance-" + index);
		$(".toggle-instance-" + index + " .toggle-item .title").click(function () {
			if ($(this).parent().parent().hasClass("is-toggle")) {

				$(this).parent().parent().find(".article").slideUp();
				$(this).parent().parent().removeClass("is-toggle");
			} else {
				$(".toggle-item").find(".article").slideUp();
				$(".toggle-item").removeClass("is-toggle");
				$(this).parent().parent().find(".article").slideDown();
				$(this).parent().parent().addClass("is-toggle");
			}
		});
	});


	if ($(".sticky-nav").length) {
		$(".sticky-nav").scrollToFixed({
			zIndex: 99,
			marginTop: 100,
			limit: function limit() {
				return (
					$("footer").offset().top -
					$("header").outerHeight() -
					100
				);
			},
			// Add the class when it becomes absolute (reaches the limit)
			preAbsolute: function () {
				$(this).addClass("reached-limit");
			},
			// Or postAbsolute, depending on exact timing preference
			// postAbsolute: function () {
			//   $(this).addClass("reached-limit");
			// },
			// Remove the class when it becomes fixed again
			preFixed: function () {
				$(this).removeClass("reached-limit");
			},
			// If you need to handle it unfixed above the scroll point
			postUnfixed: function () {
				$(this).removeClass("reached-limit");
			},
		});
	}

	$(window)
		.scroll(function () {
			var scrollDistance = $(window).scrollTop();
			// Assign active class to nav links while scolling
			$(".scroll-section-wrap .anchor-section-id").each(function (i) {
				if (
					$(this).position().top -
					$(".sticky-nav").outerHeight() -
					80 -
					1 <=
					scrollDistance
				) {
					$(".sticky-nav li.active").removeClass("active");
					$(".sticky-nav li").eq(i).addClass("active");
				}
			});
		})
		.scroll();
	$(".sticky-nav  a").on("click", function (event) {
		if (this.hash !== "") {
			let offset =
				$("header").outerHeight() + $(".sticky-nav").outerHeight();
			var hash = this.hash;
			$("html, body").animate(
				{
					scrollTop: $(hash).offset().top - offset,
				},
				800,
				function () {
					// window.location.hash = hash;
				}
			);
		} // End if
	});
	$("section").each(function () {
		const $section = $(this);
		const $items = $section.find(".tab-item");

		$items.hide(); // hide all first
		const $active = $items.filter(".active");

		if ($active.length) {
			$active.show(); // show only active one
		} else {
			// fallback: show first tab if none marked active
			$items.first().show().addClass("active");
			$section
				.find(".tab-nav li:first")
				.addClass("active")
				.siblings()
				.removeClass("active");
		}
	});
	$(".tab-nav a").on("click", function (e) {
		e.preventDefault();

		const $nav = $(this).parents(".tab-nav");
		const $section = $(this).parents("section");

		// Update active nav link
		$nav.find("li").removeClass("active");
		$(this).parents("li").addClass("active");

		const tab = $(this).attr("data-type");
		const $items = $section.find(".tab-item");

		// Hide all tabs
		$items.hide().removeClass("active");

		// Fade in selected tab
		$section.find("#" + tab).fadeIn(300).addClass("active");
	});
	//Tab hover

	$(".view-more-wrap").each(function () {
		const $wrapper = $(this);
		const initialShow = parseInt($wrapper.data('initial-show') || 4);
		const incrementBy = parseInt($wrapper.data('increment') || 6);

		let currentVisible = initialShow;
		const totalItems = $wrapper.find(".item").length;

		// Show initial items (but don't show more than what exists)
		$wrapper
			.find(".item")
			.slice(0, Math.min(currentVisible, totalItems))
			.show(function () {
				$(this).css("display", "flex");
			});

		// Hide view more button if total items <= initial show amount
		if (totalItems <= initialShow) {
			$wrapper.addClass("disable-btn");
			return; // Exit early since no "view more" functionality is needed
		}

		// Attach click handler to this specific instance
		$wrapper
			.find(".btn-primary")
			.on("click", function (e) {
				e.preventDefault();

				currentVisible += incrementBy;

				$wrapper
					.find(".item")
					.slice(0, currentVisible)
					.slideDown(function () {
						$(this).css("display", "flex");
					});

				// Hide button if all items are now visible
				if (currentVisible >= totalItems) {
					$wrapper.addClass("disable-btn");
				}
			});
	});


	//setBackground
	setBackground.forEach((attributes) => {
		attributes.style.cssText = `
		background-image: url(${attributes.getAttribute("setBackground")});
		background-size: cover;
		background-position: center;
	  `;
	});
	try {
		document.querySelectorAll('.wrap-form textarea').forEach(function (textarea) {
			textarea.value = textarea.value.trim();
		});
	} catch (error) {

	}

	if (counterItem !== null) {
		if (counterItem.length) {
			const counterUp = window.counterUp.default;
			const callback = (entries) => {
				entries.forEach((entry) => {
					const el = entry.target;
					if (
						entry.isIntersecting &&
						!el.classList.contains("is-visible")
					) {
						counterUp(el, {
							duration: 2000,
							delay: 20,
						});
						el.classList.add("is-visible");
					}
				});
			};
			const IO = new IntersectionObserver(callback, { threshold: 1 });

			counterItem.forEach((counter) => {
				IO.observe(counter);
			});
		}
	}
}


export function isSvgFile(src) {
	// Check if the source URL ends with .svg
	return src.toLowerCase().endsWith(".svg")
}



export function convertImgToSvg(imgElement) {
	// Get the src attribute of the img element
	const imgSrc =
		imgElement.getAttribute("src") || imgElement.getAttribute("data-src")
	if (!isSvgFile(imgSrc)) {
		// Alert
		console.error("Not an SVG file:", imgSrc)

		const notSvgImg = document.createElement("img")
		notSvgImg.className = "img-generate"
		notSvgImg.style.opacity = 1
		notSvgImg.src = imgSrc
		// Replace the original image element with the new one
		imgElement.parentNode.replaceChild(notSvgImg, imgElement)
		return
	}

	function setStrokeDashArrayInline(svgContent) {
		// Create a new DOM element to parse the SVG content
		const tempDiv = document.createElement("div")
		tempDiv.innerHTML = svgContent

		// Find all vector path elements in the SVG
		const vectorPaths = tempDiv.querySelectorAll("path")

		// Iterate through vector paths and calculate the stroke-dasharray
		vectorPaths.forEach((path) => {
			const isContainStrokeDasharray =
				path.getAttribute("stroke-dasharray")
			const pathLength = path.getTotalLength()
			path.style.setProperty("--data-stroke-dasharray", pathLength)
			if (isContainStrokeDasharray) return
			path.setAttribute("stroke-dasharray", pathLength)
			// console.log(pathLength);
		})

		// Return the modified SVG content
		return tempDiv.innerHTML
	}

	const observer = new IntersectionObserver((entries) => {
		entries.forEach((entry) => {
			if (entry.isIntersecting) {
				// Fetching Image SVG

				fetch(imgSrc)
					.then((response) => response.text())
					.then((svgContent) => {
						const modifiedSvgContent =
							setStrokeDashArrayInline(svgContent)
						// Create a temporary div element to parse the SVG content
						const tempDiv = document.createElement("div")
						const isError = svgContent.includes(
							"<title>Error</title>"
						)
						tempDiv.className = "svg-generate"
						if (isError)
							tempDiv.innerHTML =
								"<span class='image-not-found'>Image has been removed or moved.</span>"
						else tempDiv.innerHTML = modifiedSvgContent
						// else tempDiv.innerHTML = svgContent;

						tempDiv.setAttribute("data-src", imgSrc)
						// Replace the img element with the new svg element
						imgElement.parentNode.replaceChild(tempDiv, imgElement)

						const svgLoadedEvent = new CustomEvent("svg-loaded", {
							detail: {
								element: tempDiv,
								source: imgSrc,
								content: modifiedSvgContent,
								svgContent: svgContent,
								isError: isError,
								imgElement,
							},
						})
						document.body.dispatchEvent(svgLoadedEvent)

						// Stop observing the element after it's loaded
						observer.unobserve(imgElement)
					})
					.catch((error) => {
						console.error(
							"Error fetching or replacing image:",
							error
						)
					})
			}
		})
	})
	// Start observing the image element
	observer.observe(imgElement)
}

export function generateSVG(selector = ".image-svg") {
	const imageContainers = document.querySelectorAll(selector)
	imageContainers.forEach((imageContainer) => {
		// Find the img element within each container
		const images = imageContainer.querySelectorAll("img")
		images.forEach((img) => {
			convertImgToSvg(img)
		})
	})
	console.log('test svg');
}