

const sleep = (t) => new Promise((r) => setTimeout(r, t));

export function swiperInit() {

	var defaultSettingSwiper = {
		observer: true,
		observeParents: true,
		rewind: true,
		speed: 1205,
		loop: false,
		autoplay: {
			delay: 4000,
			pauseOnMouseEnter: true,
		},
		lazy: {
			loadPrevNext: true,
		},
	};

	function getSlidesPerGroup(slidesSizesGrid, visibleSlidesIndexes) {
		const totalSlide = slidesSizesGrid.length;
		const visibleSlide = visibleSlidesIndexes?.length ?? 1;

		let slidesPerGroup = visibleSlide;

		if (totalSlide <= visibleSlide) {
			slidesPerGroup = null;
		}
		return slidesPerGroup;
	}
	function triggerLozadNextSlideImage(swiper) {
		const nextSlide = swiper.slides[swiper.activeIndex + 1];
		if (nextSlide) {
			const nextImage = nextSlide.querySelectorAll("img");
			if (nextImage && typeof lazyLoader.triggerLoad === "function") {
				nextImage.forEach((img) => {
					lazyLoader.triggerLoad(img);
				});
			} else {
				throw new Error("Lozad is not defined");
			}
		}
	}
	function autoSetSlidesPerGroup(swiper) {
		if (!swiper.pagination.el) return;
		const { visibleSlidesIndexes, slidesSizesGrid } = swiper;
		const slidesPerGroup = getSlidesPerGroup(
			slidesSizesGrid,
			visibleSlidesIndexes
		);
		if (
			slidesPerGroup != null &&
			slidesPerGroup > 0 &&
			slidesPerGroup !== swiper.params.slidesPerGroup
		) {
			swiper.params.slidesPerGroup = slidesPerGroup;
			swiper.update();
		}
	}

	var productThumb = new Swiper(".product-thumb .swiper", {
		spaceBetween: 20,
		breakpoints: {
			200: {
				slidesPerView: 3,
				spaceBetween: 10,
			},
			476: {
				slidesPerView: 4,
			},
			768: {
				slidesPerView: 5,
			},
			1024: {
				slidesPerView: 5,
				spaceBetween: 16,
			},
			1200: {
				slidesPerView: 6,
				spaceBetween: 24,
			},
		},
		speed: 500,
		lazy: {
			loadPrevNext: true,
		},
	});
	var productMain = new Swiper(".product-main .swiper", {
		spaceBetween: 10,
		centeredSlides: true,
		speed: 1205,
		autoplay: {
			delay: 4000,
			pauseOnMouseEnter: false,
			disableOnInteraction: false,
		},
		lazy: {
			loadPrevNext: true,
		},
		navigation: {
			nextEl: ".product-thumb  .next",
			prevEl: ".product-thumb  .prev ",
		},
		thumbs: {
			swiper: productThumb,
		},
	});


	var gridSwiper = new Swiper(".grid-swiper .swiper", {
		preventInteractionOnTransition: true,
		observer: true,
		observeParents: true,
		rewind: true,
		speed: 1205,
		autoplay: {
			delay: 4000,
			pauseOnMouseEnter: true,
		},
		lazy: {
			loadPrevNext: true,
		},
		spaceBetween: 20,
		observeParents: true,
		simulateTouch: false,
		breakpoints: {
			320: {
				slidesPerView: 3,
				spaceBetween: 10,
			},
			420: {
				slidesPerView: 3,
				spaceBetween: 10,
			},

			768: {
				slidesPerView: 4,
				spaceBetween: 20,
				grid: {
					rows: 2,
					fill: "row",
				},
			},
			1024: {
				spaceBetween: 20,
				slidesPerView: 3,
				grid: {
					rows: 2,
					fill: "row",
				},
			},

			1201: {
				spaceBetween: 15,
				slidesPerView: 4,
				grid: {
					rows: 2,
					fill: "row",
				},
			},
			1600: {
				spaceBetween: 20,
				slidesPerView: 4,
				grid: {
					rows: 2,
					fill: "row",
				},
			},
		},
		navigation: {
			nextEl: ".home-partner .next",
			prevEl: ".home-partner .prev",
		},
	});



	var primarySwiper = new Swiper(".primary-banner .swiper", {
		slidesPerView: 1,
		observer: true,
		observeParents: true,
		preventInteractionOnTransition: false,
		speed: 1205,
		autoplay: false,
		lazy: {
			loadPrevNext: true,
		},
		effect: "fade",
		fadeEffect: {
			crossFade: true,
		},
		loop: true,
		init: false,
		navigation: {
			nextEl: ".primary-banner .next",
			prevEl: ".primary-banner .prev",
		},
		pagination: {
			el: ".primary-banner .swiper-pagination",
			type: "bullets",
			clickable: true,
			renderBullet: function (index, className) {
				return `<span class="${className}" data-index="${index}"></span>`;
			},
		},
	});

	let timer = null;

	// Function to handle video playback
	function handleVideoPlayback(slide) {
		if (timer) {
			clearTimeout(timer);
			timer = null;
		}

		// Pause all videos first
		const allVideos = document.querySelectorAll(".primary-banner-video");
		allVideos.forEach((video) => {
			video.pause();
			video.currentTime = 0;
		});

		const videoElement = slide.querySelector("video");
		if (videoElement) {
			// Play the current slide's video
			videoElement.play().catch((error) => {
				console.log("Video playback failed:", error);
				// Fallback to next slide if video fails to play
				timer = setTimeout(() => {
					primarySwiper.slideNext();
				}, 4000);
			});

			videoElement.addEventListener(
				"ended",
				function () {
					if (primarySwiper.slides.length === 1) {
						videoElement.currentTime = 0;
						videoElement.play();
					} else {
						primarySwiper.slideNext();
					}
				},
				{ once: true }
			);
		} else {
			// If no video, set timer for next slide
			timer = setTimeout(() => {
				primarySwiper.slideNext();
			}, 4000);
		}
	}

	primarySwiper.on("slideChangeTransitionEnd", function () {
		const currentSlide = primarySwiper.slides[primarySwiper.activeIndex];
		handleVideoPlayback(currentSlide);
	});

	primarySwiper.on("init", function () {
		const currentSlide = primarySwiper.slides[primarySwiper.activeIndex];
		handleVideoPlayback(currentSlide);
	});

	// Handle video click events
	const primaryBannerVideo = document.querySelectorAll(".primary-banner-video");
	primaryBannerVideo.forEach(function (videoElement) {
		videoElement.addEventListener("click", function (event) {
			if (this.paused) {
				this.play();
				this.parentNode.classList.remove("pause");
			} else {
				this.pause();
				this.parentNode.classList.add("pause");
			}
		});
	});

	primarySwiper.init();



	var slidingTextSwiper = new Swiper(".sliding-text-swiper .swiper", {
		loop: true,
		speed: 8000,
		spaceBetween: 0,
		slidesPerView: "auto",
		autoplay: {
			delay: 0,
			disableOnInteraction: false,
		},

	});


	window.initSwiper = [];
	$(".init-swiper .swiper").each(function (index) {
		const $this = $(this);
		let isLoop = false;
		let time = $(this).attr("data-time") || 5000;
		let isTriggerLoadedNext = false;

		if ($this.hasClass("trigger-loaded-next")) {
			isTriggerLoadedNext = true;
		}

		$this.addClass("swiper-init-" + index);
		$this.parents(".init-swiper").find(".prev").addClass("prev-nav-" + index);
		$this.parents(".init-swiper").find(".next").addClass("next-nav-" + index);
		$this
			.parents(".init-swiper")
			.find(".swiper-pagination")
			.addClass("pagination-inst-" + index);

		const options = {
			observer: true,
			observeParents: true,
			rewind: true,
			speed: 1205,
			watchSlidesProgress: true,
			lazy: {
				loadPrevNext: true,
			},
			loop: isLoop,
			autoplay: {
				delay: time,
				pauseOnMouseEnter: true,
			},
			slidesPerView: "auto",
			navigation: {
				nextEl: ".next-nav-" + index,
				prevEl: ".prev-nav-" + index,
			},
			pagination: {
				el: ".pagination-inst-" + index,
				type: "bullets",
				clickable: true,
				dynamicMainBullets: 1,
			},
			on: {
				beforeInit: function () {
					window.initSwiper.push(this);
				},
				init: function (swiper) {
					if (isLoop) {
						lozadInit();
						$(this.el)
							.find(".swiper-slide-duplicate a[data-fancybox]")
							.each(function () {
								$(this).removeAttr("data-fancybox");
							});
					}
					if (isTriggerLoadedNext) {
						triggerLozadNextSlideImage(this);
					}
				},
				slideChange: function () {
					if ($this.hasClass("slide-has-video")) {
						$this.find("video")[0].pause();
						$this.find(".swiper-slide").removeClass("play-video");
						$this.find(".play-btn").removeClass("active");
					}

					if (isTriggerLoadedNext) {
						triggerLozadNextSlideImage(this);
					}
				},
				resize: function (swiper) {
					const totalSlidesWidth = swiper.slidesSizesGrid.reduce(
						(acc, curr) => acc + curr,
						0
					);
					swiper.el.classList.toggle(
						"center-slide-active",
						totalSlidesWidth < swiper.size
					);
					autoSetSlidesPerGroup(this);
				},
			},
		};

		//  Add or remove centeredSlides based on .is-center-slide
		if ($this.hasClass("is-center-slide")) {
			options.centeredSlides = true;
		} else {
			delete options.centeredSlides;
		}

		//  Add initialSlide support (from custom attribute)
		const initialSlide = parseInt($this.attr("data-initial"), 10);
		if (!isNaN(initialSlide)) {
			options.initialSlide = initialSlide;
		}

		if (time == 0) {
			options.autoplay = {
				delay: 0,
				disableOnInteraction: true,
				pauseOnMouseEnter: false,
			};
			options.loop = true;
			options.speed = 1800;
			delete options.navigation;
		}

		var swiper_slide_auto = new Swiper(".swiper-init-" + index, {
			...options,
		});
	});


	var historySwiper = new Swiper(".history-swiper .swiper", {
		autoplay: {
			delay: 6000,
		},
		// Optional parameters
		slidesPerView: 3,
		slideToClickedSlide: true,
		breakpoints: {
			200: {
				slidesPerView: 1,
				spaceBetween: 10,
			},
			420: {
				slidesPerView: 1,
				spaceBetween: 10,
			},
			1024: {
				slidesPerView: 3,
				spaceBetween: 30,
			},
			1440: {
				spaceBetween: 60,
			},
			1600: {
				slidesPerView: 4,
				spaceBetween: 40,
			},
		},
		observer: true,
		observeParents: true,
		preventInteractionOnTransition: true,
		speed: 1205,
		lazy: {
			loadPrevNext: true,
		},
		navigation: {
			nextEl: ".history-swiper .next",
			prevEl: ".history-swiper .prev",
		},
	});








	var homeProductTop = new Swiper('.large-center-swiper .swiper', {
		slidesPerView: 1,
		spaceBetween: 10,
		rewind: true,
		centeredSlides: true,
		autoplay: {
			delay: 3000,
			disableOnInteraction: false,
		},
		loop: true,
		initialSlide: 4,
		navigation: {
			nextEl: ".large-center-swiper .next",
			prevEl: ".large-center-swiper .prev",
		},
		speed: 800,
		breakpoints: {
			320: {

				slidesPerView: 1,
				spaceBetween: 10,
				allowTouchMove: true,
			},
			1024: {
				centeredSlides: true,
				allowTouchMove: true,
				slidesPerView: 3,
				spaceBetween: 0,
				allowTouchMove: true,
			},
			1201: {
				centeredSlides: true,
				allowTouchMove: true,
				slidesPerView: 5,
				spaceBetween: 0,
			},
		},

	});


	try {
		$(".auto-center-swiper .swiper").each(function (index) {
			const $this = $(this);
			const $container = $this.parents(".auto-center-swiper");

			// Read slide-amount from parent (default: 1)
			const baseSlideAmount = parseInt($container.attr("slide-amount")) || 1;

			// Assign unique class names
			$this.addClass("swiper-init-center-" + index);
			$container.find(".prev").addClass("prev-navigation-" + index);
			$container.find(".next").addClass("next-navigation-" + index);
			$container.find(".swiper-pagination").addClass("pagination-inst-center-" + index);

			// Function to calculate responsive slidesPerView
			function getSlidesPerView() {
				const w = window.innerWidth;
				if (w < 576) return 1;
				if (w < 1024) return Math.max(1, baseSlideAmount - 1);
				return baseSlideAmount;
			}

			// Initialize Swiper
			let swiper = new Swiper(".swiper-init-center-" + index, {
				...defaultSettingSwiper,
				autoplay: {
					delay: 5000,
					pauseOnMouseEnter: false,
					disableOnInteraction: false,
				},
				slidesPerView: getSlidesPerView(),
				spaceBetween: 20,
				breakpoints: {
					200: {
						spaceBetween: 0,
					},
					1024: {
						spaceBetween: 40,
					},

				},
				navigation: {
					nextEl: ".next-navigation-" + index,
					prevEl: ".prev-navigation-" + index,
				},
				pagination: {
					el: ".pagination-inst-center-" + index,
					type: "bullets",
					clickable: true,
					dynamicMainBullets: 1,
				},
				on: {
					init: function () {
						checkSlidesAndDisable($container, $this, getSlidesPerView());
					},
				},
			});

			// Update on window resize (slidesPerView + disable check)
			window.addEventListener("resize", function () {
				const newView = getSlidesPerView();
				swiper.params.slidesPerView = newView;
				swiper.update();
				checkSlidesAndDisable($container, $this, newView);
			});
		});

		// Check if there are enough slides to enable swiper
		function checkSlidesAndDisable($container, $swiperEl, requiredSlides) {
			const totalSlides = $swiperEl.find(".swiper-slide").length;
			if (totalSlides <= requiredSlides) {
				$container.addClass("swiper-slide-disable");
			} else {
				$container.removeClass("swiper-slide-disable");
			}
		}
	} catch (error) {
		console.error(error);
	}





}